.inputDom {
    position: relative;
    width: 150px;
    height: 30px;
    line-height: 30px;
    border-radius: 8px;
    border: 1px solid #d8d8d8;
    padding-left: 5px;
}
.inputDom > input {
    border: none;
    width: 120px;
}
.inputDom > .btnDom {
    position: absolute;
    width: 20px;
    height: 30px;
    top: 0;
    right: 0;
}
.inputDom > .btnDom > .btn {
    width: 20px;
    height: 15px;
    line-height: 15px;
    text-align: center;
    cursor: pointer;
}