.nav {
  overflow: hidden;
    margin: 20px;
}
.nav > li {
    display: inline-block;
    height: 50px;
    line-height: 50px;
    font-size: 16px;
    color: red;
    margin-right: 20px;
    border: 1px solid #d8d8d8;
    border-radius: 10px;
    padding: 0 20px;
}
.nav > li.current {
    background: orange;
}
.nav > li:hover {
    background: orange;
}
.nav > li > a {
    color: #222;
    text-decoration: none;
}